import React, { Component } from "react";
class Square extends Component{
    constructor(props){
        super(props);
    }

    render(){
        if(this.props.active === 0){
            return (<div className = "border" style={{width: 4 + 'vw', height: 4 + 'vw'}}> 

                    </div>);
        } else if(this.props.active === 1){
            return (<div className = "border bg-warning" style={{width: 4 + 'vw', height: 4 + 'vw'}}> 

            </div>);
        }else {
            return (<div className = "border bg-danger" style={{width: 4 + 'vw', height: 4 + 'vw'}}> 

                    </div>);
        }
    }
}
export default Square;