import React, { Component } from "react";
import Square from "./square.jsx";
class Node{
    constructor(pos, next){
        this.next = next;
        this.pos = pos;
    }
}
class Queue{
    constructor(front, back){
        this.front = front;
        this.back = back;
    }

    push(newNode){
        newNode.next = this.back;
        this.back = newNode;
    }
    move(newFront){
        let temp = new Node(newFront, null);
        this.back = this.back.next;
        let temp2 = this.front;
        temp2.next = temp;
        this.front = temp;
        if(this.back === null){
            this.back = temp;
        }
    }

}
